:global body,
:global ul,
:global ol,
:global li,
:global p,
:global h1,
:global h2,
:global h3,
:global h4,
:global h5,
:global h6,
:global form,
:global fieldset,
:global table,
:global td,
:global img,
:global div,
:global dl,
:global dt,
:global dd,
:global input {
  margin: 0;
  padding: 0;
}
:global body {
  font-size: 12px;
}
:global html,
:global body {
  height: 100%;
}
:global img {
  border: none;
}
:global ul,
:global ol,
:global li {
  list-style: none;
}
:global input,
:global select,
:global textarea {
  outline: none;
}
:global textarea {
  resize: none;
}
:global a {
  text-decoration: none;
}
:global table {
  border-collapse: collapse;
  border-spacing: 0;
}
:global .clearfix:after {
  content: "";
  display: block;
  clear: both;
}
:global .clearfix {
  zoom: 1;
}
:global .fl {
  float: left;
}
:global .fr {
  float: right;
}
:global .wrap {
  margin: 0 auto;
}
:global .my-custom-antd-mask {
  position: relative;
  opacity: 0.9;
}
:global .my-custom-antd-mask #myCustomAntdMask {
  position: absolute;
  width: 20px;
  height: 20px;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  z-index: 2001;
}
:global .my-custom-antd-mask #myCustomAntdMask:before,
:global .my-custom-antd-mask #myCustomAntdMask:after {
  content: '';
  border-radius: 50%;
  background-color: #2db7f5;
  animation: antSpinBounce 2.2s infinite ease-in-out;
  display: block;
  position: absolute;
  opacity: 0.5;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
:global .my-custom-antd-mask #myCustomAntdMask:after {
  animation-delay: -1.1s;
}
:global .login-modal .anticon,
:global .login-modal .ant-confirm-title {
  display: none;
}
:global .login-modal .ant-confirm-body .ant-confirm-content {
  margin-left: 0;
}
:global .login-modal .ant-confirm-btns {
  display: none;
}
:global .login-modal .login-iframe iframe {
  width: 100%;
  height: 370px;
}
:global .custom-form-invalid-explain {
  position: absolute;
  bottom: 42px;
  right: 0px;
  background-color: #f7eaea;
  border-radius: 4px;
  border: 1px solid #e9c3c0;
  padding: 2px 6px;
  font-weight: 600;
  box-shadow: 3px 3px 8px #999;
}
:global .custom-form-invalid-explain:before {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  border-width: 4px 4px 0;
  bottom: -5px;
  left: 9px;
  border-style: solid;
  border-color: #e9c3c0 transparent transparent;
}
:global .custom-form-invalid-explain:after {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  border-width: 4px 4px 0;
  bottom: -4px;
  left: 9px;
  border-style: solid;
  border-color: #f7eaea transparent transparent;
}
:global .ant-drawer-body .ant-collapse-content-box {
  padding: 6px;
}
:global .ant-drawer-body .ant-form-item {
  margin-bottom: 0px;
}
:global .ant-drawer-body .ant-row {
  display: flex;
}
:global .ant-drawer-body .ant-row .ant-form-item {
  margin-bottom: 0px;
}
:global .ant-drawer-body .ant-row .ant-form-item .ant-form-item-label {
  width: 120px;
}
:global .ant-drawer-body .ant-row .ant-form-item .ant-form-item-control-wrapper {
  width: 200px;
}
:global .ant-drawer-body button.ant-btn {
  margin-top: 20px;
  margin-right: 20px;
}
