.hotline-widget {
  position: fixed;
  z-index: 1000;
  right: 200px;
  border: 1px solid #ccc;
  background-color: #fff;
  box-shadow: 0px 0px 6px 1px #adbcc4;
}
.hotline-widget .sdk-container .next-loading .next-loading-component .next-icon-boda {
  display: none;
}
.next-balloon-normal {
  display: none;
}
.ant-modal-title {
  font-size: 16px;
}
.modal-form .ant-modal-body .ant-form-item,
.modal-form .ant-drawer-body .ant-form-item {
  display: flex;
  width: 90%;
  margin-bottom: 8px;
}
.modal-form .ant-modal-body .ant-form-item .ant-form-item-label,
.modal-form .ant-drawer-body .ant-form-item .ant-form-item-label {
  width: 140px;
}
.modal-form .ant-modal-body .ant-form-item .ant-form-item-control-wrapper,
.modal-form .ant-drawer-body .ant-form-item .ant-form-item-control-wrapper {
  width: 100%;
}
.modal-form .ant-modal-body .ant-form-item .ant-input-number,
.modal-form .ant-drawer-body .ant-form-item .ant-input-number {
  width: 130px;
}
.login-modal {
  z-index: 2000;
}
.login-modal .login-container .ant-form-item-control {
  height: 50px;
}
.login-modal .login-container .register-area {
  text-align: right;
}
.login-modal .login-container .register-area span {
  color: orange;
}
.login-modal .login-container .verify-code .ant-form-item-children {
  display: flex;
}
.login-modal .login-container .verify-code .ant-form-item-children img {
  width: 100px;
}
.login-modal .login-container .verify-code .ant-form-item-children input {
  flex: 1;
  margin-right: 14px;
}
.first-login-c {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 1;
}
.first-login-c .logo-text {
  position: fixed;
  top: 20px;
  left: 200px;
  color: #fff;
  font-size: 64px;
  z-index: 2;
}
.first-login-c .ant-carousel .carousel-item {
  height: 100vh;
}
.first-login-c .ant-carousel .carousel-item-1 {
  background-image: url('../../../../images/bk1.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}
.first-login-c .ant-carousel .carousel-item-2 {
  background-image: url('../../../../images/bk2.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}
.first-login-c .ant-carousel .carousel-item-3 {
  background-image: url('../../../../images/bk3.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}
.first-login-c .ant-carousel .carousel-item-4 {
  background-image: url('../../../../images/bk4.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}
#uedit-container {
  margin-top: 20px;
}
