.contact-form .contact-c {
  margin-top: 4px;
}
.contact-form .contact-c .ant-form-item-control {
  width: 120px;
}
.contact-form .contact-c .contact-item {
  position: relative;
  padding: 10px 10px 6px 0;
}
.contact-form .contact-c .contact-item .ant-form-item {
  margin-bottom: 0;
}
.contact-form .contact-c .contact-item i.anticon-close {
  position: absolute;
  right: 6px;
  top: 6px;
  font-size: 14px;
}
