html,
body {
  font-family: "Microsoft YaHei";
}
.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
#sportRoot {
  opacity: 1;
}
#sportRoot .ant-menu {
  color: #000;
}
#sportRoot .ant-menu .ant-menu-submenu-title {
  font-size: 14px;
}
#sportRoot .ant-menu .ant-menu-item {
  font-size: 14px;
}
#sportRoot .ant-tabs.ant-tabs-card .ant-tabs-card-bar .ant-tabs-tab-active {
  color: #08a4ff;
  font-weight: bold;
}
#sportRoot .ant-table,
#sportRoot .ant-form {
  color: rgba(0, 0, 0, 0.85);
}
/*::-webkit-scrollbar
{
	width: 19px;
	height: 16px;
	background-color: #ccc;
}*/
/*定义滚动条轨道 内阴影+圆角*/
.product-env .ant-btn-primary,
.product-env .rel-title a,
.product-env .quick-test {
  color: red!important;
  font-weight: 600!important;
}
.product-env .custom-script-editor .CodeMirror {
  background-color: #690404;
}
.ant-steps-title {
  animation-name: 'buttonLight';
  animation-duration: 1s;
  animation-timing-function: cubic-bezier;
  animation-iteration-count: infinite;
}
.ant-table-small {
  display: table;
  width: 100%;
}
.product-domain-tooltip {
  word-break: break-all;
}
.var-null-val {
  color: #aaa;
}
.FBH {
  display: flex;
}
.fc .product {
  background: red!important;
}
.fc .fc-top {
  display: flex;
  background: #1DA57A;
  height: 40px;
}
.fc .fc-top .server {
  padding-left: 20px;
  font-size: 14px;
}
.fc .fc-top .server > span {
  margin-left: 6px;
  padding: 4px;
  border-radius: 4px;
}
.fc .fc-top .server .product-domain-text:hover {
  font-weight: 600;
  cursor: pointer;
}
.fc .fc-top .server .sys-groups {
  color: yellow;
  font-size: 12px;
}
.fc .fc-top .user-info {
  flex: 1;
  text-align: right;
  line-height: 40px;
  color: #eee;
  font-size: 12px;
  min-width: 140px;
}
.fc .fc-top .user-info a {
  color: #eee;
  margin-left: 20px;
  margin-right: 20px;
}
.fc .fc-top .user-info > span {
  cursor: pointer;
}
.fc .fc-top > svg {
  fill: #e8f69e;
  font-size: 26px;
  margin-left: 10px;
  vertical-align: -6px;
}
.fc .fc-top > span {
  color: #e8f69e;
  font-size: 18px;
  line-height: 40px;
  padding-left: 12px;
  letter-spacing: 1px;
  padding-right: 20px;
}
.fc .fc-top > span a {
  color: #fff;
}
.fc .fc-top > i {
  color: #f3d57c;
  margin-left: 10px;
  font-size: 10px;
  font-style: normal;
  border-radius: 50%;
  border: 1px solid #f3d57c;
  padding: 2px 4px 3px 4px;
  vertical-align: 2px;
}
.fc .fc-top .prod {
  color: #fff;
  border: 1px solid red;
  font-size: 16px;
  background-color: red;
}
.fc .fc-main {
  display: flex;
}
.fc .fc-main .hidden {
  display: none;
}
.fc .fc-main .fc-menu-container {
  width: 160px;
  background-color: #c0e1ff38;
  height: 100vh;
  user-select: none;
}
.fc .fc-main .fc-menu-container .logo-c {
  height: 50px;
  position: relative;
  z-index: 1;
  font-size: 20px;
  text-align: center;
  line-height: 40px;
  color: #fff;
  background-color: #08a4ff;
  background-image: url('\../\../\../images/header.jpg');
}
.fc .fc-main .fc-menu-container .logo-c > svg {
  width: 42px;
  height: 42px;
  top: 4px;
  left: -52px;
  position: relative;
  fill: #fff;
}
.fc .fc-main .fc-menu-container .logo-c .logo-text {
  position: absolute;
  width: 120px;
  left: 36px;
  top: 4px;
  font-weight: 500;
}
.fc .fc-main .fc-menu-container svg {
  width: 24px;
  height: 24px;
  margin-left: -12px;
  vertical-align: -6px;
  margin: 0 6px;
  fill: #555;
}
.fc .fc-main .fc-menu-container .ant-menu {
  background-color: inherit;
}
.fc .fc-main .fc-menu-container .ant-menu .ant-menu-inline .ant-menu-submenu-title {
  margin-top: 0;
  margin-bottom: 0;
}
.fc .fc-main .fc-menu-container .ant-menu .ant-menu-inline .ant-menu-item {
  margin-bottom: 0;
  margin-top: 0;
  width: calc(100% + 2px);
}
.fc .fc-main .fc-menu-container .ant-menu .ant-menu-inline .ant-menu-item span {
  line-height: 44px;
}
.fc .fc-main .fc-menu-container .ant-menu .ant-menu-inline .ant-menu-item:hover {
  color: #08a4ff;
}
.fc .fc-main .fc-menu-container .ant-menu .ant-menu-inline .ant-menu-item:hover svg {
  fill: #08a4ff;
}
.fc .fc-main .fc-menu-container .ant-menu .ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected {
  background-color: #fff;
}
.fc .fc-main .fc-menu-container .ant-menu .ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected svg {
  fill: #08a4ff;
}
.fc .fc-main .fc-menu-container .ant-menu .ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected span {
  color: #00639d;
  font-weight: bold;
}
.fc .fc-main .fc-menu-container .ant-menu > .ant-menu-submenu-selected {
  color: #00639d !important;
}
.fc .fc-main .fc-menu-container .ant-menu > .ant-menu-submenu-selected > .ant-menu-submenu-title {
  font-weight: bold;
}
.fc .fc-main .fc-container {
  flex: 1;
  padding: 6px 10px 0 0px;
  position: relative;
  overflow-x: hidden;
}
.fc .fc-main .fc-container .fc-header {
  height: 60px;
  background-color: #08a4ff;
  position: absolute;
  width: calc(100VW);
  top: -10px;
  left: -90px;
  background-image: url('\../\../\../images/header.jpg');
}
.fc .fc-main .fc-container > .ant-tabs {
  padding-top: 4px;
}
.fc .fc-main .fc-container > .ant-tabs .ant-tabs-nav {
  user-select: none;
  font-size: 14px;
}
.fc .fc-main .fc-container > .ant-tabs .ant-tabs-nav .tab-title > svg {
  width: 1.3em;
  height: 1.3em;
  margin-right: 6px;
  position: relative;
  top: 2px;
}
.fc .fc-main .fc-container > .ant-tabs > .ant-tabs-content {
  padding-left: 20px;
}
.fc .fc-main .fc-container .ant-tabs-bar {
  margin-bottom: 10px;
  border-bottom: none;
}
.fc .fc-main .fc-container .ant-tabs-tab-active {
  border-bottom: solid 1px #fff;
  background-color: #fff;
}
.fc .fc-main .fc-container .login-sec {
  position: absolute;
  right: 8px;
  top: 6px;
  color: #fff;
  z-index: 1;
}
.fc .fc-main .fc-container .login-sec .logout-a {
  color: #fff;
  margin-left: 20px;
  margin-right: 10px;
  position: relative;
}
.fc .fc-main .fc-container .opt-container {
  font-size: 12px;
}
.fc .fc-main .fc-container .opt-container a:hover {
  text-decoration: underline;
}
.fc .json-field .ant-form-item-control {
  position: relative;
}
.fc .json-field .ant-form-item-control .button-area {
  position: absolute;
  bottom: 0px;
  right: 10px;
  user-select: none;
}
.fc .json-field .ant-form-item-control .button-area a {
  margin-left: 10px;
}
.avatar-tooltip .ant-tooltip-content .ant-tooltip-arrow::before {
  background-color: #fff;
}
.avatar-tooltip .ant-tooltip-content .ant-tooltip-inner {
  background-color: #fff;
  width: 376px;
  height: 314px;
}
.avatar-image-c {
  width: 360px;
  height: 300px;
  background-size: cover;
  background-position: center;
}
