.ant-drawer-body .ant-form .ant-form-item .ant-form-item-control-wrapper {
  flex: 1;
}
.ant-drawer-body .ant-form > .ant-row {
  display: flex;
}
.ant-drawer-body > .ant-btn {
  margin-right: 18px;
}
.form-table .ant-table-small > .ant-table-content > .ant-table-body {
  margin: 0;
}
.form-table .ant-table-thead > tr > th:first-child {
  text-align: center;
}
.form-table .table-opt-col {
  position: relative;
  text-align: center;
}
.form-table .table-opt-col .btns {
  position: absolute;
  display: flex;
  left: -6px;
  top: 1px;
}
.form-table .table-opt-col .btns button {
  width: 16px;
  height: 17px;
  margin-left: 4px;
  margin-top: 0;
  margin-right: 0;
}
.form-table .table-opt-col .btns button i {
  position: relative;
  left: -6px;
  top: -2px;
}
.form-table .table-opt-col .btns button i svg {
  fill: #999;
}
.form-table .ant-table-row:hover .show-off {
  opacity: 1;
  transition: opacity 0.3s linear;
}
.form-table .ant-table-row:hover .show-off-i {
  opacity: 1;
}
.form-table .ant-table-row:hover .idx {
  opacity: 0;
}
.form-table .show-off {
  opacity: 0;
  transition: opacity 0.3s linear;
}
.form-table .show-off-i {
  opacity: 0;
}
.form-table .ant-select-focused {
  opacity: 1;
}
