.save-btn-pop {
  padding-bottom: 6px;
}
.save-btn-pop .ant-popover {
  width: 150px;
}
.save-btn-pop li {
  display: flex;
  line-height: 22px;
}
.save-btn-pop li .ant-input {
  width: 110px;
  margin: 0 4px;
  padding: 1px;
  height: 22px;
}
.save-btn-pop .save-item {
  display: flex;
  margin-top: 6px;
}
.save-btn-pop .save-item:hover {
  font-weight: 500;
}
.save-btn-pop .save-item span {
  padding-right: 4px;
}
.save-btn-pop .save-item .save-name-label {
  width: 80px;
  text-overflow: ellipsis;
  overflow: hidden;
}
.input-params-pop .json-editor {
  width: 400px;
}
.input-params-pop button {
  margin-right: 20px;
}
.editable-field-c {
  overflow: hidden;
}
.editable-field-c a {
  color: #08a4ff !important;
}
.editable-field-c i.anticon {
  margin-left: 18px;
}
.editable-field-c svg {
  font-size: 14px;
  color: #08a4ff;
}
.editable-field-c .ant-input {
  padding: 0;
  padding-left: 4px;
  font-size: 12px;
  height: 24px;
}
