.indicates-c .ant-form-item {
  display: flex;
  margin-bottom: 0px;
}
.indicates-c .ant-collapse > .ant-collapse-item > .ant-collapse-header {
  padding: 2px 4px;
  padding-left: 34px;
}
.indicates-c .ant-collapse-content > .ant-collapse-content-box {
  padding: 2px;
  padding-left: 6px;
}
.indicates-c .search-label .ant-form-item-label {
  width: 100px;
  text-align: left;
}
.indicates-c .search-label .ant-form-item-label label {
  top: -2px;
}
.indicates-c .search-label .ant-form-item-control {
  line-height: 32px;
}
.indicates-c .search-label .ant-form-item-control-wrapper {
  flex: 1;
}
.indicates-c .search-label .clear-search-cond {
  position: absolute;
  right: -4px;
}
.indicates-c ul {
  display: flex;
  flex-wrap: wrap;
  position: relative;
}
.indicates-c ul li {
  margin-right: 22px;
  display: block;
  line-height: 34px;
}
.indicates-c ul li .range-c {
  display: flex;
  align-items: center;
}
.indicates-c ul li .range-c > i {
  color: #999;
}
.indicates-c ul li.Range {
  display: flex;
  align-items: center;
}
.indicates-c ul li.Input .ant-input {
  width: 140px;
}
.indicates-c ul li.Area {
  display: flex;
  align-items: center;
}
.indicates-c ul .ant-input-number {
  width: 120px;
}
.indicates-c ul .ant-select {
  width: 140px;
}
.indicates-c ul .ant-calendar-picker {
  width: 290px;
}
.ant-select-selection--multiple ul {
  display: block;
}
.area-c {
  display: flex;
}
.area-c .ant-select {
  margin-right: 1px;
}
.save-btn-pop-customer-search {
  width: 220px;
}
.save-btn-pop-customer-search input.ant-input {
  width: 126px;
}
